  <div class="bg-gradient-primary">
    <div class="container">
      <!-- Outer Row -->
      <div class="row justify-content-center">
        <div class="col-xl-10 col-lg-12 col-md-9">
          <div class="card o-hidden border-0 shadow-lg my-5">
            <div class="card-body p-0">
              <!-- Nested Row within Card Body -->
              <div class="row">
                <div class="col-lg-6 d-none d-lg-block bg-login-image"></div>
                <div class="col-lg-6">
                  <div class="p-5">
                    <div class="text-center">
                      <h1 class="h4 text-gray-900 mb-4">
                        Bem-vindo ao Gerenciador de Tarefas do MANY
                      </h1>
                    </div>
                    <form
                      class="user"
                      @submit.prevent="loginRequest"
                      id="login-form"
                    >
                      <div class="form-group">
                        <input
                          type="email"
                          v-model="ViewModel.email"
                          id="email"
                          class="form-control form-control-user"
                          aria-describedby="emailHelp"
                          placeholder="Enter Email Address..."
                          required
                        />
                        <!-- id="exampleInputEmail" -->
                      </div>
                      <div class="form-group">
                        <input
                          type="password"
                          class="form-control form-control-user"
                          placeholder="Password"
                          v-model="ViewModel.password"
                          id="password"
                          required
                        />
                      </div>
                      <div class="form-group">
                        <div class="custom-control custom-checkbox small">
                          <input
                            type="checkbox"
                            class="custom-control-input"
                            v-model="ViewModel.continuarConectado"
                            id="customCheck"
                          />
                          <label class="custom-control-label" for="customCheck"
                            >Continuar conectado?</label
                          >
                        </div>
                      </div>
                      <button
                        type="submit"
                        class="btn btn-primary btn-user btn-block"
                      >
                        Login
                      </button>
                      <hr />
                      <div v-if="mensage" class="alert alert-warning" role="alert">
                       {{ mensage }}
                      </div>
                      <!-- <a
                        href="index.html"
                        class="btn btn-google btn-user btn-block"
                      >
                        <i class="fab fa-google fa-fw"></i> Login with Google
                      </a>
                      <a
                        href="index.html"
                        class="btn btn-facebook btn-user btn-block"
                      >
                        <i class="fab fa-facebook-f fa-fw"></i> Login with
                        Facebook
                      </a> -->
                    </form>
                    <!-- <hr /> -->
                    <div class="text-center">
                      <button class="small btn" @click="resetar()">
                        Esqueci a senha
                      </button>
                    </div>
                    <div class="text-center">
                      <router-link to="signup" class="small btn"
                        >Criar conta</router-link
                      >
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="errorMessage !== ''" class="alert alert-danger" role="alert">
        {{ errorMessage }}
      </div>
      <div
        v-if="successMessage !== ''"
        class="alert alert-success"
        role="alert"
      >
        {{ successMessage }}
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
import { ref } from "vue";
import { useRouter } from "vue-router";
import Swal from "sweetalert2";

export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      xhrRequest: false,
      errorMessage: "",
      successMessage: "",
      continuarConectado: false,
    };
  },
  setup() {
    let router = useRouter();
    let mensage = ref("");
    let ViewModel = ref({
      email: "",
      password: "",
      continuarConectado: ref(false),
    });
    const loginRequest = () => {
      mensage.value = "";
      firebase
        .auth()
        .signInWithEmailAndPassword(
          ViewModel.value.email,
          ViewModel.value.password
        )
        .then(
          (resp) => {
            if (ViewModel.value.continuarConectado) {
              window.localStorage.setItem(
                "refreshToken",
                resp.user.refreshToken
              );
            }
            router.push("/dashboard");
            console.log(resp.user.refreshToken);
          },
          (error) => {
            console.log("erro", error.message);
            mensage.value = error.message;
          }
        );
    };
    const resetar = () => {
      Swal.fire({
        icon: "warning",
        title: `Atenção`,
        text: `Digite o email para redefinição de senha`,
        input: "email",
        confirmButtonText: `Fechar`,
      }).then((response) => {
        if (response.isConfirmed) {
          firebase
            .auth()
            .sendPasswordResetEmail(response.value)
            .then(() => {
              Swal.fire({
                icon: "success",
                title: `Sucesso`,
                text: `Um e-mail com o link para redefinição de senha foi enviado para: "${response.value}`,
                confirmButtonText: `Enviar`,
              });
            })
            .catch((error) => {
              console.log("erro", error);
            });
        }
      });
    };
    return {
      ViewModel,
      loginRequest,
      resetar,
      mensage
    };
  },
  methods: {
    // loginRequest() {
    //   let v = this;
    //   v.xhrRequest = true;
    //   v.errorMessage = "";
    //   v.successMessage = "";
    //   firebase
    //     .auth()
    //     .signInWithEmailAndPassword(v.email, v.password)
    //     .then(
    //       (resp) => {
    //         if (this.continuarConectado) {
    //           window.localStorage.setItem(
    //             "refreshToken",
    //             resp.user.refreshToken
    //           );
    //         }
    //         // this.$router.replace("dashboard");
    //         // v.xhrRequest = false;
    //         console.log(resp.user.refreshToken);
    //       },
    //       (error) => {
    //         v.errorMessage = error.message;
    //         v.xhrRequest = false;
    //       }
    //     );
    // },
  },
};
</script>
<style scoped>
.loader {
  position: relative;
  top: 6px;
  left: 10px;
}
